body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#selectedBlogs {
  font-size: 1.5em;
  color: #009A8A;
}

#image {
  float: right;
}

#email {
  word-wrap: break-word;
}

.portfolioIFrameShowSmall {
  width: 80%;
}

.portfolioIFrameShowRegular {
  width: 560px;
  height: 315px;
}

.portfolioImageShowSmall {
  width: 80%;
}

.portfolioImageShowRegular {
  width: 560px;
}

.dropDownItem a {
  color: #009A8A !important;
}

.blurbStyle {
  font-size: 1.5em;
  color: gray;
}

.contact-body {
  font-size: 1.5em;
}

.dropDownItem a:hover {
  color: #007D9A !important;
}

.tarjeta:hover {
  background-color: #009A8A !important;
  cursor: pointer !important;

}

.tarjeta:active {
  background-color: #007D9A !important;
  cursor: pointer !important;
}

#Root:hover {
  color: #007D9A !important;
  cursor: pointer !important;
}

